      top: 5px;
      border: 1px solid #2e344e;
      height: 15px;
      width: 15px;
      line-height: 1;
      font-size: 13px; }
  input[type=checkbox]:checked ~ label,
  input[type=radio]:checked ~ label {
    color: #037fff; }
